.about-container {
    background-image: url("/images/6.png");
    background-size: cover;
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0px;
    left: 0px;
    z-index: -1;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .about-info {
    width: 60vw;
    height: 60vh;
    background-color: #ffffff85;
    border-radius: 5%;
    padding: 5px 5px 5px 10px;
  }
  
  .about-info .title{
    color : rgb(27, 2, 87);
    text-align: center;
    font-size:30px;
  }

  .about-info p{
    font-size:22px;
    font-family: 'Roboto Condensed', sans-serif;
  }
  

.SlideShow{
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.middle{
    position: relative;
    /* top: 50%;
    left: 50%;
    transform: translate(-50%,-50%); */
}

.nav{
    position: absolute;
    bottom: 20px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    display: flex;
}

.bar{
    width: 100px;
    height: 15px;
    border: 2px solid gold;
    margin: 10px;
    cursor: pointer;
    -webkit-transition:  0.4s;
    transition:  0.4s;
}

.bar:hover{
    background: gold;
}

input[name="r"]{
    position: absolute;
    visibility: hidden;
}

.slides{
    width:500%;
    height: 100%;
    display: flex;
}

.slide{
    width: 20%;
    -webkit-transition: 0.6s;
    transition: 0.6s;
}
.slide img{
    width: 100%;
    height: 100%;
}

#r1:checked ~ .s1{
    margin-left: 0;
}
#r2:checked ~ .s1{
    margin-left: -20%;
}
#r3:checked ~ .s1{
    margin-left: -40%;
}
#r4:checked ~ .s1{
    margin-left: -60%;
}
#r5:checked ~ .s1{
    margin-left: -80%;
}

.product {
  /* width: 300px; */
  /* border-radius: 5px; */
  border: 1px solid #0000003f;
  background-color: #000000;
  text-align: center;
  /* margin: 0px 20px 20px 20px; */
  /* padding: 25px 10px 20px 10px; */
  /* -webkit-box-shadow: 0px 0px 29px 1px rgba(33, 33, 33, 1);
  -moz-box-shadow: 0px 0px 29px 1px rgba(33, 33, 33, 1); */
  /* box-shadow: 0px 0px 29px 1px rgba(33, 33, 33, 1); */
  position: relative;
}

.product img {
  width: 225px;
  height: 225px;
}

.product-title{
  color: #fff;
  font-family: "Balsamiq Sans", cursive;
}

.product-price{
  color: #fff;
  font-family: "Balsamiq Sans", cursive;
}

.add-to-cart {
  background-color: #ffd700;
  border: none;
  font-size: 18px;
  border-radius: 5px;
  padding: 5px 10px 5px 10px;
  color: white;
  font-family: "Balsamiq Sans", cursive;
  -webkit-transition: all 0.3s cubic-bezier(0.445, 0.05, 0.55, 0.95);
  transition: all 0.3s cubic-bezier(0.445, 0.05, 0.55, 0.95);
  cursor: pointer;
}

.add-to-cart:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  box-shadow: 0px 0px 15px 1px rgb(255, 255, 255);
}

.notification-container{
  display: none;
  position: fixed;
  width: 100%;
  left:25%;
  top:20px;
  opacity: 1;
  -webkit-transition: 2s opacity;
  transition: 2s opacity;
}

.fade-out {
  opacity: 0;
}

.notification {
  display: flex;
  padding: 15px;
  border: 1px solid black;
  margin: 10px;
  width: 50%;
  align-items: center;
  background-color: rgba(255, 255, 255, 0.678);
}

.notification-message {
  font-family: "Balsamiq Sans", cursive;
  font-size: 22px;
}

.tick {
  margin-right: 10px;
}

.tick img {
  width: 40px;
}
.close {
  margin-left: auto;
}

.close img {
  width: 20px;
  cursor: pointer;
}


