.cart-container {
  text-align: center;
}

.cart-title{
  font-family: "Balsamiq Sans", cursive;
  font-size: 32px;
}

.cart-item {
  margin: auto;
  border: 1px solid;
  color: rgb(255, 215, 0);
  padding: 15px;
  margin-top: 10px;
  margin-bottom: 20px;
  width: 80%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.cart-item.cart-header {
    margin-bottom: 20px;
    
}

.cart-item div {
  /* border-left: 1px solid white; */
  text-align: center;
  font-size: 22px;
  font-weight: 700;
}

.cart-item img {
  width: 100%;
  height: 200px;
}

.cart-header div:nth-child(1),
.cart-item div:nth-child(1) {
  width: 20%;
  border-left: none;
}
.cart-header div:nth-child(2),
.cart-item div:nth-child(2) {
  width: 20%;
}
.cart-header div:nth-child(3),
.cart-item div:nth-child(3) {
  width: 15%;
}
.cart-header div:nth-child(4),
.cart-item div:nth-child(4) {
  width: 15%;
}
.cart-header div:nth-child(5),
.cart-item div:nth-child(5) {
  width: 10%;
}
.cart-header div:nth-child(6),
.cart-item div:nth-child(6) {
  width: 15%;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
